<template>
  <div class="rating-pag">
      <head-top head-title="修改用户名" goBack="true"></head-top>
      <section class="setname">
          <section class="setname-top">
              <!-- @input监听输入框里面的值是否发生变化 -->
              <input type="text" placeholder="输入用户名" :class="{'setname-input':bordercolor}" @input="inputThing" v-model="inputValue" name="" id="">
              <div>
                  <p v-if="earn">用户名只能修改一次(5-24个字符)</p>
                  <p v-else class="unlikep">用户名长度5-24个字符之间</p>
              </div>
          </section>
          <section class="reset">
              <button @click="resetName" :class="{fontopacity:opacityall}">确认修改</button>
          </section>
      </section>
      <alert-tip v-if="showAlert" @closeTip="showAlert = false" :alertText="alertText"></alert-tip>
  </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import {getImgPath} from '../../../components/common/mixin'
import headTop from '../../../components/header/header'
import alertTip from '../../../components/common/alertTip'
export default {
    mixins: [getImgPath],
    components: {
        headTop,
        alertTip
    },methods: {
        ...mapMutations([
            'RETSET_NAME'
        ]),
        inputThing()
        {
            if(this.inputValue.length<5 || this.inputValue.length>24)
            {
                this.bordercolor=true;
                this.earn=false;
                this.opacityall=false;
            }else{
                this.bordercolor=false;
                this.earn=true;
                this.opacityall=true;
            }
        },
        resetName()
        {
            if(this.bordercolor || this.inputValue.length==0)
            {
                this.alertText="用户名输入不合法请重新输入";
                this.showAlert=true;
            }else{
                this.RETSET_NAME(this.inputValue);
                this.$router.go(-1);
            }
        }
    },computed: {
        ...mapState([
            'userInfo'
        ])
    },data(){
        return{
            earn:true,//输入框提醒
            bordercolor:false,//输入框颜色
            opacityall:null,//字体透明度
            inputValue:'',//输入框内容
            newusername:'',//新用户名
            showAlert:false,
            alertText:null,
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.rating-pag{
    position: fixed;
    top:0;
    bottom: 0;
    left:0;
    right:0;
    background: #f5f5f5;
    z-index: 150;
        p, span{
        font-family: Helvetica Neue,Tahoma,Arial;
    }
}
.setname{
    margin-top: 1.8rem;
    .setname-top{
        padding:0.4rem;
        input{
            @include wh(100%,1.8rem);
            background: #f5f5f5;
            border:1px solid #ccc;
            font-size: 0.7rem;
            padding:0.2rem;
        }
        .setname-input{
            border-color: red;
        }
        .unlikep{
            color:red;
        }
    }
    div{
        margin-top: 0.3rem;
        p{
            @include sc(0.4rem,#333);
        }
    }
    .reset{
        margin-top: 0.5rem;
        text-align: center;
        button{
            @include wh(90%,2.3rem);
            background: #3190e8;
            color:#fff;
            font-size: 0.7rem;
        }
    }
}

</style>



